import React from "react";
import bridge from "@vkontakte/vk-bridge";
import qs from "querystring";
import { Route, HashRouter, Switch, NavLink } from "react-router-dom";
import Transition from "react-transition-group/Transition";

import InfoPage from "./InfoPage";
import UserPage from "./UserPage";
import DepressionTest from "./DepressionTest";
import EmpathyTest from "./EmpathyTest";
import ColorLoveTest from "./ColorLoveTest";
import EQTest from "./EQTest";
import SelfValueTest from "./SelfValueTest";
import AnxietyTest from "./AnxietyTest";
import PersonTypeTest from "./PersonTypeTest";

import "../App.css";

import testsInfo from "../data/testsInfo.js";

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      testInfo: "",
      headerStyles: {},

      show: false,
    };

    this.getTests = this.getTests.bind(this);
    this.setModalText = this.setModalText.bind(this);
    this.getHeaderStyle = this.getHeaderStyle.bind(this);
    this.showAnimation = this.showAnimation.bind(this);
    this.openMainApp = this.openMainApp.bind(this);
  }

  componentDidMount() {
    this.setState({ headerStyles: this.getHeaderStyle() });

    this.showAnimation();
  }

  showAnimation() {
    this.setState({ show: true });
  }

  getHeaderStyle() {
    const str = window.location.search.slice(1);
    const objParams = qs.parse(str);

    let platform = objParams.vk_platform;

    if (platform === "mobile_iphone") {
      return {
        header: {
          height: "70px",
          paddingTop: "30px",
        },
        body: {
          paddingTop: "20px",
        },
      };
    } else {
      return {};
    }
  }

  setModalText(text) {
    this.setState({ testInfo: text });
  }

  openMainApp() {
    bridge.send("VKWebAppOpenApp", { app_id: 7646928 });
  }

  getTests() {
    let response = testsInfo.map((test, i) => {
      return (
        <div key={i}>
          <Transition in={this.state.show} timeout={100 + i * 150}>
            {(state) => {
              return (
                <div
                  className={"testView-" + state}
                  style={{ backgroundColor: test.color }}
                >
                  <div className="testTitle">{test.title}</div>
                  <button
                    type="button"
                    className="infoBtn"
                    style={{ backgroundColor: test.color }}
                    data-bs-toggle="modal"
                    data-bs-target="#infoModal"
                    onClick={(e) => this.setModalText(test.text, e)}
                  >
                    <i className="fas fa-info-circle"></i> инфо
                  </button>
                  <button
                    type="button"
                    className="infoBtn"
                    style={{ backgroundColor: test.color }}
                    onClick={() =>
                      bridge.send("VKWebAppShare", {
                        link: `https://vk.com/app7713167#${test.url}`,
                      })
                    }
                  >
                    <i className="fas fa-share-square"></i> отправить
                  </button>
                  <br />
                  <NavLink to={test.url}>
                    <button
                      type="button"
                      className="testComeInBtn"
                      style={{ borderColor: test.color, color: test.color }}
                    >
                      пройти тест
                    </button>
                  </NavLink>
                  <div className="testCount">
                    кол-во вопросов: {test.questionsCount}
                  </div>
                </div>
              );
            }}
          </Transition>
        </div>
      );
    });

    return response;
  }

  render() {
    let tests = this.getTests();
    let styles = this.state.headerStyles;

    return (
      <div>
        <div className="modal fade" id="infoModal" tabIndex="-1" role="dialog">
          <div className="modal-dialog modal-dialog-scrollable modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">О тесте</h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">
                <div className="testText">{this.state.testInfo}</div>
              </div>
            </div>
          </div>
        </div>

        <div className="Header" style={styles.header}>
          <span className="titleMark" onClick={this.openMainApp}>
            Мαú
          </span>{" "}
          <span className="titleApp">тесты</span>
          {/* <HashRouter>
            <NavLink className="linkStyle" to="/info">
              <span className="headerBtn">
                <i className="fas fa-info-circle"></i>
              </span>
            </NavLink>
          </HashRouter> */}
        </div>
        <div className="Body" style={styles.body}>
          <HashRouter>
            <Switch>
              <Route exact path="/">
                {tests}
              </Route>
              <Route exact path="/info">
                <InfoPage />
              </Route>
              <Route exact path="/profile">
                <UserPage />
              </Route>
              <Route exact path="/test-depression">
                <DepressionTest name="Тест на уровень депрессии" id={1} />
              </Route>
              <Route exact path="/test-empathy">
                <EmpathyTest name="Тест на уровень эмпатии" id={0} />
              </Route>
              <Route exact path="/test-colorlove">
                <ColorLoveTest name="Как ты любишь" id={2} />
              </Route>
              <Route exact path="/test-eq">
                <EQTest
                  name="Тест на уровень эмоционального интеллекта"
                  id={3}
                />
              </Route>
              <Route exact path="/test-selfvalue">
                <SelfValueTest name="Тест на уровень самоценности" id={4} />
              </Route>
              <Route exact path="/test-anxiety">
                <AnxietyTest name="Тест на уровень тревожности" id={5} />
              </Route>
              <Route exact path="/test-persontype">
                <PersonTypeTest name="Тест на тип личности" id={6} />
              </Route>
            </Switch>
          </HashRouter>
          <a
            href="https://vk.com/warmay"
            className="linkStyle"
            target="_blank"
            rel="noopener noreferrer"
          >
            <div className="copyrightText">Май</div>
          </a>
        </div>
        <div className="footer">
          <HashRouter>
            <NavLink className="linkStyle" to="/info">
              <div className="btnFooter">
                <i className="fas fa-info-circle"></i>
              </div>
            </NavLink>
            <NavLink className="linkStyle" to="/">
              <div className="btnFooter">
                <i className="fas fa-home"></i>
              </div>
            </NavLink>
            <NavLink className="linkStyle" to="/profile">
              <div className="btnFooter">
                <i className="fas fa-user"></i>
              </div>
            </NavLink>
          </HashRouter>
        </div>
      </div>
    );
  }
}

export default Main;
